import { useState } from 'react';
import axios from 'axios';

const API_BASE = process.env.REACT_APP_API_BASE;

export default function Contact() {
  const [form, setForm] = useState({ name: '', email: '', message: '' });
  const [status, setStatus] = useState(null);

  const onChange = e => setForm({ ...form, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    setStatus('sending');
    try {
      await axios.post(`${API_BASE}/api/contact`, form);
      setStatus('sent');
      setForm({ name: '', email: '', message: '' });
    } catch (err) {
      setStatus('error');
    }
  };

  return (
    <main style={{ padding: '2rem' }}>
      <h2>Contact Me</h2>
      <form onSubmit={onSubmit} style={{ display: 'grid', gap: '1rem', maxWidth: 400 }}>
        <input name="name" placeholder="Name" value={form.name} onChange={onChange} required />
        <input name="email" type="email" placeholder="Email" value={form.email} onChange={onChange} required />
        <textarea name="message" placeholder="Message" value={form.message} onChange={onChange} required />
        <button type="submit">Send</button>
      </form>
      {status === 'sending' && <p>Sending...</p>}
      {status === 'sent' && <p>Message sent successfully ✅</p>}
      {status === 'error' && <p>Something went wrong ❌</p>}
    </main>
  );
}